import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
//import Child from "./Child"
import Player from "./player"
import login from "./login";
//import JsonRead from "./jsonread"
import selectvideos from "./selectVideos";
//import moveLinkBoxer from "./boxercise";
//import moveLinkHiit from "./hiit";
//import moveLinkYoga from "./yoga";
import {MoveLink} from "./exercise";
import result from "./result";
import Videos from "./videos.json"

const App = () => {
  const homeUrl = process.env.PUBLIC_URL
  const videosData = Videos.data
  return (
    <BrowserRouter>
      <Switch>
        {/* <Route exact path="/" component={JsonRead} /> */}
        <Route exact path={homeUrl + "/"} component={login} />
        <Route exact path={homeUrl + "/selectvideos"} component={selectvideos} />
        {
          videosData.map((val, i) => {
            return (
              <Route exact path={homeUrl + videosData[i]["path"]} render={()=><MoveLink path={videosData[i]["datapath"]} index={i} />} />
            )
          })
        }
        <Route exact path={homeUrl + "/result"} component={result} />
      </Switch>
    </BrowserRouter>
  );
}

