import React, { Component } from 'react';
import { Player, ControlBar, BigPlayButton } from "video-react";
import "video-react/dist/video-react.css";
import video_sample from "./sample/video.mp4";
import video_yoga from "./yoga/video.mp4";
import video_bts from "./btsDynamite/video.mp4"
import video_bts_short from "./btsDynamiteShort/video.mp4"
import video_baseball from "./baseball/video.mp4"
import video_box from "./box/video.mp4"
import video_niziu from "./niziu/video.mp4"
import video_paprika from "./paprika/video.mp4"
import video_ebikani from "./ebikani/video.mp4"
import video_edison from "./edison/video.mp4"
import video_kitsune from "./kitsune/video.mp4"
import posterSample from "./sample/poster.png"
import posterYoga from "./yoga/poster.png"
import posterBts from "./btsDynamite/poster.png"
import posterBtsShort from "./btsDynamiteShort/poster.png"
import posterBaseball from "./baseball/poster.png"
import posterBox from "./box/poster.png"
import posterNiziu from "./niziu/poster.png"
import posterPaprika from "./paprika/poster.png"
import posterEbikani from "./ebikani/poster.png"
import posterEdison from "./edison/poster.png"
import posterKitsune from "./kitsune/poster.png"
// import { Button } from 'reactstrap';
import Button from "@material-ui/core/Button";

class VideoPlayer extends Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            currentTime: 0,
            ended: false,
            count: 0,
        };
        if(this.props.path == "./sample"){
            this.state.videoSrc = `${video_sample}`
            this.state.posterSrc = `${posterSample}`
        } else if (this.props.path == "./yoga"){
            this.state.videoSrc = `${video_yoga}`
            this.state.posterSrc = `${posterYoga}`
        } else if (this.props.path == "./btsDynamite"){
            this.state.videoSrc = `${video_bts}`
            this.state.posterSrc = `${posterBts}`
        } else if (this.props.path == "./btsDynamiteShort"){
            this.state.videoSrc = `${video_bts_short}`
            this.state.posterSrc = `${posterBtsShort}`
        } else if (this.props.path == "./baseball"){
            this.state.videoSrc = `${video_baseball}`
            this.state.posterSrc = `${posterBaseball}`
        } else if (this.props.path == "./box"){
            this.state.videoSrc = `${video_box}`
            this.state.posterSrc = `${posterBox}`
        } else if (this.props.path == "./niziu"){
            this.state.videoSrc = `${video_niziu}`
            this.state.posterSrc = `${posterNiziu}`
        } else if (this.props.path == "./paprika"){
            this.state.videoSrc = `${video_paprika}`
            this.state.posterSrc = `${posterPaprika}`
        } else if (this.props.path == "./ebikani"){
            this.state.videoSrc = `${video_ebikani}`
            this.state.posterSrc = `${posterEbikani}`
        } else if (this.props.path == "./edison"){
            this.state.videoSrc = `${video_edison}`
            this.state.posterSrc = `${posterEdison}`
        } else if (this.props.path == "./kitsune"){
            this.state.videoSrc = `${video_kitsune}`
            this.state.posterSrc = `${posterKitsune}`
        }
        this.play = this.play.bind(this);
        this.getCurrentTime = this.getCurrentTime.bind(this);
        this.getState = this.getState.bind(this);
    }
    
    componentDidMount() {
        // subscribe state change
        this.player.subscribeToStateChange(this.handleStateChange.bind(this));
    }

    handleStateChange(state) {
        // copy player state to this component's state
        this.setState({
            player: state,
        });
    }

    play() {
        this.player.playbackRate = this.props.speed; 
        this.player.play();
    }

    getCurrentTime() {
        const { player } = this.player.getState();
        this.setState( {currentTime: player.currentTime})//, count: this.state.count+1})
        this.props.setCurrentTime(this.state.currentTime)
    }

    getState() {
        const { player } = this.player.getState();
        this.setState( {ended: player.ended})//, count: this.state.count+1})
        this.props.setPlayerState(this.state.ended)
    }

    componentDidUpdate() {
        // this.player.subscribeToStateChange(this.getCurrentTime);
        this.player.subscribeToStateChange(this.getState);
        if(this.props.play){
            if(this.state.count===0){
                this.play()
                this.setState( {count: this.state.count+1})
            }

        }
        //console.log(this.state.videoSrc)
    }

    render() {
        //this.player.subscribeToStateChange(this.getCurrentTime);
        return (
            <div>
                <Player
                    ref={player => {
                        this.player = player;
                    }}
                    // autoPlay
                    playsInline={true}
                    poster={this.state.posterSrc}
                >
                    <source src={this.state.videoSrc} type="video/mp4"/>
                    <BigPlayButton disabled />
                    <ControlBar disabled />
                </Player>
                {/* <p>{this.state.currentTime}</p> */}
                {/* <Button onClick={this.play} className="mr-3">
                    Play!!
                </Button> */}
            </div>
        );
    }
}

export default VideoPlayer;