import thumbKitsune from "./kitsune/thumbnail.png"

const selectVideos = () => {
  const homeUrl = process.env.PUBLIC_URL
  const videosData = Videos.data
  const thumbs = []
  for(let i in videosData){
    if(videosData[i]["datapath"] == "./sample"){
      thumbs.push(`${thumbSample}`)
    } else if (videosData[i]["datapath"] == "./yoga"){
      thumbs.push(`${thumbYoga}`)
    } else if (videosData[i]["datapath"] == "./btsDynamite"){
      thumbs.push(`${thumbBts}`)
    } else if (videosData[i]["datapath"] == "./btsDynamiteShort"){
      thumbs.push(`${thumbBtsShort}`)
    } else if (videosData[i]["datapath"] == "./baseball"){
      thumbs.push(`${thumbBaseball}`)
    } else if (videosData[i]["datapath"] == "./box"){
      thumbs.push(`${thumbBox}`)
    } else if (videosData[i]["datapath"] == "./niziu"){
      thumbs.push(`${thumbNiziU}`)
    } else if (videosData[i]["datapath"] == "./paprika"){
      thumbs.push(`${thumbPaprika}`)
    } else if (videosData[i]["datapath"] == "./ebikani"){
      thumbs.push(`${thumbEbikani}`)
    } else if (videosData[i]["datapath"] == "./edison"){
      thumbs.push(`${thumbEdison}`)
    } else if (videosData[i]["datapath"] == "./kitsune"){
      thumbs.push(`${thumbKitsune}`)
    }
  }
  console.log(thumbs)
  return (
    <section style={bkbackground}>
      <ThemeProvider theme={darktheme}>
        <AppBar position="static">
          <Toolbar>
            <Link to={homeUrl + `/`} style={linktext}>
              <Button color="inherit">BACK</Button>
            </Link>
          </Toolbar>
        </AppBar>
        <Grid container>
          <Grid item xs={12}>
            <Box my={3} style={titletext} textAlign="center">
              Select Video
            </Box>
          </Grid>
          {
            videosData.map((val, i) => {
              return (
                <Grid item xs={12} sm={6} md={6} lg={4}>
                  <Box mx={3} textAlign="center">
                    <Link to={homeUrl + videosData[i]["path"]}>
                      <img
                        src={thumbs[i]}
                        alt={videosData[i]["title"]}
                        style={{
                          "max-width":"100%",
                          "height":"auto"
                        }}
                      />
                    </Link>
                  </Box>
                  <Box color="white" textAlign="center" mb={3}>
                    {videosData[i]["title"]}
                  </Box>
                </Grid>
              )
            })
          }
          {/* <Grid item xs={12} sm={6} md={6} lg={4}>
            <Box textAlign="center">
              <Link to={homeUrl + "/exercise-sample"}>
                <img
                  src="https://img.youtube.com/vi/FOgKMAtcNnE/mqdefault.jpg"
                  alt="tes"
                />
              </Link>
            </Box>
            <Box color="white" textAlign="center">
              Sample Test
            </Box>
          </Grid>
          <Grid item xs={12} sm={6} md={6} lg={4}>
            <Box textAlign="center">
              <Link to={homeUrl + "/exercise-yoga"}>
                <img
                  src="https://img.youtube.com/vi/8fJk2ZLRoIE/mqdefault.jpg"
                  alt="tes"
                />
              </Link>
            </Box>
            <Box color="white" textAlign="center">
              Yoga
            </Box>
          </Grid> */}
        </Grid>
      </ThemeProvider>
    </section>
  )
}


export default selectVideos;
